<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
import store from './store';
import { watch } from 'vue'
export default {
  name: 'App',
  components: {},
  setup() {
    const user= JSON.parse(sessionStorage.getItem('CurUser'))
    watch(
      () => store.state.menu, // observe expression
      (val, old)=> {
        if (!old && user && user.no) {
          store.commit('setRouter',val)
        }
      },
      { immediate: true } // options: immediate option
    )},
}
</script>

<style>
#app {
  height: 100%;
}
</style>
